.form {
    width: 320px;
  }
  
  .label {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
  }

  .button {
    display: flex;
    align-items: center;
    justify-content: center;
  
    width: 320px;
    height: 30px;
    margin-bottom: 50px;
  
    background-color: var(--accent-bg-color);
    color: var(--second-text-color);
    font-family: inherit;
    border: none;
    /* border-radius: 10px; */
  
    cursor: pointer;
    transition: background-color var(--main-hover-animation);
  }
  
  .button:hover,
  .button:focus {
    background-color: var(--main-text-color);
  }
  
  .button__text {
    margin-right: 10px;
  }

